const Content = styled.div`
    width: 100%;
    padding-bottom: 200px;
    font-size: 14px;
`;

const Banner = styled.div`
    position: relative;
    margin-bottom: 59px;
    padding: 0 30%;
    width: 100%;
    line-height: 0.85;
    & > img {
        width: 100%;
    }
    & > div {
        width: 100%;
        & > div > img {
            width: 100%;
        }
    }
    & > h2 {
        font-size: 34px;
        width: 100%;
        text-align: center;
        color: black;
        position: absolute;
        top: 59px;
    }
`;

const Desc = styled.div`
    padding: 0 15%;
    & > div {
        display: flex;
        margin-bottom: 60px;
        & > div:nth-child(1) {
            width: calc(20% - 45px);
            margin-right: 45px;

            font-size: 18px;
            font-weight: bold;
            font-style: normal;
            font-stretch: normal;
            line-height: 1.61;
            letter-spacing: normal;
            color: #000000;
        }
        & > div:nth-child(2) {
            width: 80%;
            & > h1 {
                margin-bottom: 34px;
                font-size: 32px;
                font-weight: bold;
                font-style: normal;
                font-stretch: normal;
                line-height: normal;
                letter-spacing: normal;
                color: #000000;
            }

            & > p {
                font-size: 14px;
                font-weight: normal;
                font-style: normal;
                font-stretch: normal;
                line-height: 1.71;
                letter-spacing: normal;
            }

            & > img {
                width: 100%;
                height: 430px;
                background-color: #aaa;
            }
        }
    }
`;

const Wrapper = styled.div`
    width: 100%;
    height: 0;
    padding-bottom: 100%;
    position: relative;
    & > img {
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        position: absolute;
        /* & > img {
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            width: 100%;
            height: 100%;
        } */
    }

    & > div {
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        position: absolute;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;

        & > img {
            width: 10% !important;
            height: 10%;
        }
    }
`;

const ExDiv = styled.img`
    /* background: -webkit-linear-gradient(left, #ba59a6 0%, #69aed8 50%, #1c12a8 100%); */
    mask-image: ${(props: { offset: number }) =>
        `linear-gradient(to right, rgba(0,0,0,1) ${props.offset - props.offset / 2.5}%, rgba(0,0,0,0) ${props.offset}%)`};
    clip-path: ${(props: { offset: number }) =>
        `polygon(0 0, ${props.offset}% 0, ${props.offset}% 100%, 0% 100%); circle(${props.offset}% at 0 0)`};
`;

const DragAbleImage = props => {
    const [dragging, setDragging] = React.useState(false);
    const [pos, setPos] = React.useState({ rel: 0, pos: 0, width: 0 });

    const onMouseDown = e => {
        if (e.button !== 0) {
            return;
        }
        const elemPos = document.getElementById("elem")!.getBoundingClientRect();
        setDragging(true);
        setPos({ ...pos, rel: e.pageX - elemPos.left, width: elemPos.right - elemPos.left });

        e.stopPropagation();
        e.preventDefault();
    };

    const onMouseMove = e => {
        if (!dragging) {
            return;
        }
        const elemPos = document.getElementById("elem")!.getBoundingClientRect();
        setPos({ ...pos, pos: ((e.pageX - elemPos.left) / pos.width) * 150 });
        e.stopPropagation();
        e.preventDefault();
    };
    const onMouseUp = e => {
        setDragging(false);
        e.stopPropagation();
        e.preventDefault();
    };
    return (
        <Wrapper id={"elem"} onMouseDown={onMouseDown} onMouseMove={onMouseMove} onMouseUp={onMouseUp}>
            <img src={require("../../assets/project/영남빌딩_과거.png")} alt="" />
            <ExDiv offset={pos.pos} src={require("../../assets/project/영남빌딩_현재.png")} />
            {/* <div>
                <img src={require("../../assets/drag-right.png")} alt="" />
                오른쪽으로 드래그 해보세요!
            </div> */}
        </Wrapper>
    );
};

export default class Seocho extends React.Component<SeochoProps, any> {
    public render() {
        return (
            <>
                <Navigation
                    list={[{ name: "홈", link: "/" }, { name: "프로젝트", link: "/project/" }, { name: "서초", link: "/seocho/" }]}
                />
                <Content>
                    <Banner>
                        {/* <img src={require("../../assets/영남빌딩_과거.png")} alt="" /> */}
                        <DragAbleImage />
                    </Banner>
                    <Desc>
                        <div>
                            <div>서초 영남빌딩</div>
                            <div>
                                <h1>보전과 개발의 조화, 영남빌딩</h1>
                                <p>
                                    영남빌딩은 서울시 서초구 서초대로 56길 18번지에 있다. <br />
                                    영남빌딩이 위치한 서초동 교대역 인근은 오래 전부터 대입 재수생들에게 등용문이자, <br />
                                    공부에 지쳐 고단한 잠을 청하는 요람이었다. 서초대로 이면 도로에 자리한 영남빌딩 또한 <br />
                                    재도약을 준비하는 학생들을 위한 숙소로써 20여 년 동안 자리를 지켜왔다. <br />
                                    <br />
                                    하지만 2010년대 중반에 접어들며 재수학원들이 점차 서초동에서 노량진, 강남 일대로 이전하면서 <br />
                                    90년대부터 2010년 초반까지 입시생들의 추억이 묻은 교대 인근 학사들은 기억 속으로 사라져갔다. <br />
                                    <br />
                                    그리고 2018년. 서울올림픽이 한창이던 때에 건축된 이 건물은 <br />
                                    30년 만에 현대적이고 세련 된 디자인을 가진 호스텔 건물로 재탄생 되었다. <br />
                                    <br />
                                    학생들의 보금자리였던 층은 한국을 찾는 외국인 관광객들의 따뜻한 잠자리로 바뀌었고, <br />
                                    여름이면 수박을 베어 물던 넓은 마루와 빨간 샌드백이 있던 옥상은 투숙객들의 소셜라이 징 공간으로 재탄생
                                    되었다. <br />
                                    이른 새벽이면, 밥 짓는 냄새에 고픈 배를 달래며 내려가던 지하의 학생 식당은 <br />
                                    서초동의 새로운 놀이터, 유니언 PC카페로 다시 태어났다. <br />
                                    <br />
                                    옛 모습을 지우고 더 높이 지을 수도 있었다. 하지만 모두가 벽돌을 하늘과 가까이 높힐 때, <br />
                                    영남빌딩은 역사의 보전과 새로운 공간 개발의 조화로움 속에 새롭게 태어났다.
                                </p>
                            </div>
                        </div>
                    </Desc>
                </Content>
            </>
        );
    }
}



